nav {
  background-color: #181818;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px; /* Add padding to the nav bar */
  position: fixed; /* Fix position to the top of the window */
  top: 0; /* Align to the top */
  left: 0; /* Align to the left */
  right: 0; /* Stretch right to fill the full width */
  z-index: 1000; /* Ensure it's above other content */
  background: linear-gradient(to bottom, #181818, rgba(24, 24, 24, 0));
}

.nav-bar-items-container {
  z-index: 1002;
  display: flex;
  justify-content: space-between;
}



@media (min-width:751px){

  .menu {
    cursor: pointer;
    height: 100px; /* Default height for larger screens */
    width: auto;
    display: block;
  }
}

@media (max-width:750px){

  .menu {
    cursor: pointer;
    height: 60px; /* Default height for larger screens */
    width: auto;
    display: block;
  }

}

@media (min-width:751px){

  .hermesview {
    height: 100px; /* Default height for larger screens */
    width: auto;
    position: absolute;
    right: 0;
  }

}

@media (max-width:751px){

  .hermesview {
  height: 50px; /* Default height for larger screens */
  width: auto;
  position: absolute;
  right: 0;
  }

}

.mobile-menu ul {
  list-style: none;
  padding: 0;
  margin: auto; /* Center the list items vertically */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Center the list items horizontally */
  height: 100%; /* Set the height to 100% */
}

.mobile-menu ul li {
  margin: 10px 0;
}

.mobile-menu ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 700;
  font-family: 'Your-Fancy-Font', sans-serif;
  transition: color 0.3s ease;
}

.mobile-menu ul li a:hover,
.mobile-menu ul li a:focus {
  color: black;
  font-weight: bold;
}

.social-media-list {
  position: absolute;
  bottom: 0;
  margin: 20px; /* Add some margin for spacing */
}

.social-media-list a {
  margin-right: 20px; /* Increase the spacing between social media icons */
  font-size: 2.5rem; /* Increase the font size */
  color: #fff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.social-media-list a:hover,
.social-media-list a:focus {
  color: black;
}

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top left, #8a5a00, #181818);
  z-index: 1000;
  opacity: 0;
  pointer-events: none; /* Add this to prevent clicks on the overlay when it's not visible */
  transition: opacity 0.5s ease-out; /* Add a transition for a smooth fade effect */
}

.mobile-menu-overlay.visible {
  opacity: 1;
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1001;
}


@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
  
}

.no-scroll {
  overflow: hidden;
}